
import Vue from 'vue'
import { candidateApi } from '@/api'
import { Candidate } from '@/api/candidate'

export default Vue.extend({
  name: 'TSSimpleData',
  data () {
    return { candidates: [] as Candidate[] }
  },
  async created () {
    const response = await candidateApi.getCandidates()
    this.candidates = response.data
  }
})
